@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:300,400,700|Taviraj:300,400');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

@font-face {
  font-family: 'PseudoSaudi';
  src: local('PseudoSaudi'), url(./Fonts/PSAUDI5.ttf) format('truetype'),
    url(./Fonts/PSAUDI5.woff) format('woff'); // Chrome, Firefox
}

@font-face {
  font-family: 'AlfredDrake';
  src: local('AlfredDrake'), url(./Fonts/AlfredDrake.ttf) format('truetype'),
    url(./Fonts/AlfredDrake.woff) format('woff'); // Chrome, Firefox
}

html,
body,
.root {
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  font-size: 1rem;
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Roboto Slab', sans-serif;
    font-size: 250%;
  }
}
